import { Pagination, Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import './styles.css';
import useWindowSize from '../hooks/useWindowSize';

interface Props {
  resoultionsSlideCount?: { desktop: number; mobile: number };
  imgs?: unknown[];
  showArrows?: boolean;
}
const Slider = ({ showArrows, imgs, resoultionsSlideCount = { desktop: 3, mobile: 2 } }: Props) => {
  const { width } = useWindowSize();
  const { desktop, mobile } = resoultionsSlideCount;
  const slidesCount = (width ?? 0) < 600 ? mobile : mobile;
  return (
    <>
      <Swiper
        slidesPerView={slidesCount}
        spaceBetween={0}
        slidesPerGroup={slidesCount}
        loop={true}
        loopFillGroupWithBlank={true}
        pagination={{
          clickable: true,
        }}
        navigation={showArrows ? true : (width ?? 0) > 400}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        {imgs?.map((img, idx) =>
          typeof img === 'string' ? (
            <SwiperSlide key={img}>
              <img src={img} />
            </SwiperSlide>
          ) : (
            <SwiperSlide key={idx}>{img as JSX.Element}</SwiperSlide>
          )
        )}
      </Swiper>
    </>
  );
};

export default Slider;
