import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import DataObjIcon from '@mui/icons-material/DataObject';
import { useState, MouseEvent } from 'react';
import Path from 'routes/paths';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import WhatsAppButton from 'components/WhatsAppButton';

const pages = [
  { name: 'Projects', path: Path.Projects },
  { name: 'About', path: Path.About },
  { name: 'Contact', path: Path.Contact },
  { name: 'Resume', path: Path.Resume },
];

const MobileBar = () => {
  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  return (
    <Bar
      sx={{ backgroundColor: 'black' }}
      style={{
        fontFamily: 'Quicksand',
        backgroundImage: 'linear-gradient(to left, rgba(0,0,0,0),rgba(0, 140, 255, 0.3) 90%)',
      }}
      position="fixed"
    >
      <Toolbar style={{ fontFamily: 'Quicksand' }} disableGutters>
        <Box sx={{ flexGrow: 1, display: 'flex' }}>
          <IconButton
            size="large"
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleOpenNavMenu}
            color="inherit"
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorElNav}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={Boolean(anchorElNav)}
            onClose={handleCloseNavMenu}
            sx={{
              display: { xs: 'block' },
            }}
          >
            {pages.map(({ name, path }) => (
              <Link key={name} to={path}>
                <MenuItem key={name} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{name}</Typography>
                </MenuItem>
              </Link>
            ))}
          </Menu>
        </Box>
      </Toolbar>
    </Bar>
  );
};
export default MobileBar;

const Bar = styled(AppBar)`
  display: none !important;
  @media screen and (max-width: 720px), (max-height: 630px) {
    // MOBILE VIEW
    display: block !important;
  }
`;
